import axios from "../utils/axiosLogin.js"
import axiosCwellsapi from "../utils/axiosCwellsapi.js"
export const Get_Cwells_Api = () => {
  return axiosCwellsapi.get("apiservice/apiservice.asmx/GetCityList")
}
export const Get_City_Json = () => {
  return axios.get("wxapp/public/city.json")
}

export const Get_City_Int_Json = () => {
  return axios.get("wxapp/public/city-int.json")
}

export const Get_City_Int2_Json = () => {
  return axios.get("wxapp/public/city-int2.json")
}
