<template>
  <div class="flight">
    <div class="page_title">
      <span class="left">
        <van-icon name="arrow-left" size="25" @click="$router.go(-1)" />
      </span>
      <span>
        <img src="../../assets/image/domestic_flight.png" alt="" class="flightImg" />
      </span>
      <span v-if="$route.path == '/flight'" class="flightTopspan">
        国内航班
        <br />
        Domestic Air
      </span>
      <span v-else class="flightTopspan">
        国际航班
        <br />
        International Air
      </span>
    </div>
    <div class="page_cont">
      <div class="routertype">
        <div>
          <div class="routetype_t">
            <div v-for="(v, i) in this.$route.path == '/flight'
                  ? routetype_t
                  : routetype_s" :key="i" @click="router_active = i" :class="{ router_active: router_active == i }">
              {{ v.title }}
              <br />
              <span>
                {{ v.entitle }}
              </span>
              <hr v-if="router_active == i" />
            </div>
          </div>
          <!--第一程  -->
          <div class="search-cont">
            <div class="cont" :class="router_active == 2 ? 'contInt' : ''">
              <div class="internation-box" v-if="
                    $route.path == '/internationalflight' && router_active == 2
                  "></div>
              <div class="flag flag-left" v-if="
                    $route.path == '/internationalflight' && router_active == 2
                  ">
                {{ multipassNum[0] }}
              </div>
              <div style="padding: 0 20px">
                <van-col span="10">
                  <span>出发地/From</span>
                  <van-field readonly clickable v-model="selectedCityText" placeholder="选择城市" @click="handleClick" />
                </van-col>
                <van-col span="4">
                  <div class="change_city">
                    <img src="../../assets/image/chage.png" alt="" @click="changeSelectedCityText" />
                  </div>
                </van-col>
                <van-col span="10" class="aliginright">
                  <span>目的地/To</span>
                  <van-field readonly clickable v-model="selectedCityText1" placeholder="选择城市" @click="handleClick1" />
                </van-col>
              </div>
            </div>

            <div class="cont" style="padding: 0 20px">
              <van-col span="10">
                <span>出发日期/Depart</span>
                <van-cell readonly clickable :value="date_D[0]" placeholder="选择日期" @click="
                      router_active == 0
                        ? (showPickerOne = true)
                        : (showPickerTwo = true);

                      date_n = 0;
                    " />
              </van-col>
              <van-col span="4"></van-col>
              <van-col span="10" class="aliginright">
                <span v-show="router_active == 1">返程日期/Return</span>
                <van-cell v-show="router_active == 1" readonly clickable :value="date_D[1]" placeholder="选择日期" @click="
                      router_active == 1
                        ? (showPickerTwo = true)
                        : (showPickerOne = true);
                      date_n = 1;
                    " />
              </van-col>
            </div>
            <div class="cont" style="padding: 0 20px" v-if="router_active == 0 || router_active == 1">
              <van-col span="12">
                <span>舱位/Class</span>
                <van-field readonly clickable v-model="cabin" placeholder="选择舱位" @click="showPicker3 = true" />
              </van-col>
            </div>
          </div>
        </div>
      </div>
      <div class="travrler">
        <psg-cont></psg-cont>
      </div>
    </div>
    <div class="pup">
      <van-popup class="select_popup" v-model:show="showPicker" round position="bottom">
        <van-search v-model="searchValue" placeholder="请输入城市名" />
        <!--  value-key="CityNameChinese" -->
        <van-picker show-toolbar allow-html confirm-button-text="确认/confirm" cancel-button-text="取消/cancel" :columns-field-names="keyName" :columns="getSearchcities()" @confirm="onConfirm" @cancel="showPicker = false" />
      </van-popup>
      <div>
        <van-calendar v-model:show="showPickerOne" @confirm="onConfirmOne" :show-confirm="false" color="#044db5" title="日期选择/Date selection" />
        <van-calendar v-model:show="showPickerTwo" type="range" @confirm="onConfirmTwo" color="#044db5" title="日期选择/Date selection" confirm-text="完成/Confirm" confirm-disabled-text="请选择结束时间/Select end time" :formatter="formatter" />
        <!-- confirm-disabled-text 确认按钮处于禁用状态时的文字 -->
      </div>

      <van-popup class="select_popup" v-model:show="showPicker3" round position="bottom">
        <van-picker confirm-button-text="确认/confirm" cancel-button-text="取消/cancel" show-toolbar allow-html :columns="cabins" @confirm="onConfirm3" @cancel="showPicker3 = false" />
      </van-popup>
    </div>
    <div class="next" @click="searchb" :class="router_active == 2 ? 'IntNext' : ''">
      <span>下一步<br />Next Step</span>
    </div>
  </div>
</template>

<script>
import { Get_Cwells_Api, Get_City_Json, Get_City_Int_Json, Get_City_Int2_Json } from "../../api/getCityList.js";
import psgCont from '../psgcontPage/psgCont.vue'
export default {
  name: 'flight',
  data () {
    return {
      showPicker: false,
      showPicker2: false,
      showPickerOne: false,
      showPickerTwo: false,
      showPicker3: false,
      cities_n: 0,
      cities_n2: 0,
      date_n: 0,
      citylist: [],
      searchCities: [],
      // 出发地
      selectedCityText: "",
      // 目的地
      selectedCityText1: "",
      router_active: 0,
      keyName: {
        text: 'keyName',
        value: 'keyName',
      },
      routetype_t: [
        { title: "单程", entitle: "One Way" },
        { title: "往返程", entitle: "Round Trip" },
      ],
      routetype_s: [
        { title: "单程", entitle: "One Way" },
        { title: "往返程", entitle: "Round Trip" },
        // { title: "多程", entitle: "Multiple Pass" },
      ],
      searchValue: "",
      date_D: [
        new Date().format("yyyy-MM-dd"),
        new Date(new Date().getTime() + 24 * 3600 * 1000).format("yyyy-MM-dd"),
      ],
      date_picker: [
        new Date(),
        new Date(new Date().getTime() + 24 * 3600 * 1000 * 360 * 10),
        new Date(),
      ],
      cabin: "经济舱/Economy Class",
      cabins: [
        { text: '经济舱/Economy Class', value: 'economy' },
        { text: '公务舱/Business Class', value: 'business' },
        { text: '头等舱/First Class', value: 'first' },
      ],
      cities: [
        {
          LocIdCity: "SHA",
          id: "184",
          CityNameChinese: "上海",
          CityNameEnglish: "SHANGHAI",
          keyName: "上海/SHANGHAI",
        },
        {
          id: "172",
          LocIdCity: "BJS",
          CityNameChinese: "北京",
          CityNameEnglish: "BEIJING",
          keyName: "北京/BEIJING",
        },
      ],
      cities_int: [
        {
          CityNameChinese: "上海",
          CityNameEnglish: "SHANGHAI",
          LocIdCity: "SHA",
          id: "184",

          AIRPORT_CODE: "SHA",
          AIRPORT_NAME: "上海虹桥机场",
          AIRPORT_NAME_EN: "SHANGHAI HONGQIAO AIRPORT",
          CITY_CODE: "SHA",
          CITY_NAME: "上海",
          CITY_NAME_EN: "Shanghai",
          CTRY_CODE: "CN",
          CTRY_NAME: "中国",
          CTRY_NAME_EN: "CHINA",
        },
        {
          id: "182",
          LocIdCity: "TYO",
          CityNameChinese: "东京",
          CityNameEnglish: "Tokyo",

          AIRPORT_CODE: "TYO",
          AIRPORT_NAME: "东京",
          AIRPORT_NAME_EN: "TOKYO",
          CITY_CODE: "TYO",
          CITY_NAME: "东京",
          CITY_NAME_EN: "Tokyo",
          CTRY_CODE: "JP",
          CTRY_NAME: "NULL",
          CTRY_NAME_EN: "NULL",
        },
      ],
      citiesInt: [
        {
          CityNameChinese: "上海",
          CityNameEnglish: "SHANGHAI",
          LocIdCity: "SHA",
          id: "184",
        },
        {
          id: "182",
          LocIdCity: "GJ",
          CityNameChinese: "东京",
          CityNameEnglish: "Tokyo",
        },
      ],
      citiesInt2: [
        {
          id: "182",
          LocIdCity: "GJ",
          CityNameChinese: "东京",
          CityNameEnglish: "Tokyo",
        },
        {
          id: "172",
          LocIdCity: "BJS",
          CityNameChinese: "北京",
          CityNameEnglish: "BEIJIN",
        },
      ],
      citiesInt3: [
        {
          id: "182",
          LocIdCity: "GJ",
          CityNameChinese: "东京",
          CityNameEnglish: "Tokyo",
        },
        {
          id: "172",
          LocIdCity: "BJS",
          CityNameChinese: "北京",
          CityNameEnglish: "BEIJIN",
        },
      ],
      citiesInt4: [
        {
          id: "182",
          LocIdCity: "GJ",
          CityNameChinese: "东京",
          CityNameEnglish: "Tokyo",
        },
        {
          id: "172",
          LocIdCity: "BJS",
          CityNameChinese: "北京",
          CityNameEnglish: "BEIJIN",
        },
      ],
    }
  },
  created () {
    this.getcitylist()
    this.updateSelectedCityText();
    this.updateSelectedCityText1();
    let a = this.$store.state.CltId_set;
    // console.log('aaaaaaaaflight', a);
  },
  computed: {
    currentCity () {
      return this.$route.path === '/flight' ? this.cities[0] : this.cities_int[0];
    },
    currentCity1 () {
      return this.$route.path === '/flight' ? this.cities[1] : this.cities_int[1];
    },
  },
  watch: {
    // 监听 cities_n 的变化，更新 selectedCityText
    // cities_n: "updateSelectedCityText",
    // 监听 $route.path 的变化，更新 selectedCityText
    // $route: "updateSelectedCityText",
  },
  components: {
    psgCont,
  },
  methods: {
    async getcitylist () {
      const [cwellsData, cityJsonData, cityIntJsonData, cityInt2JsonData] = await Promise.all([Get_Cwells_Api(), Get_City_Json(), Get_City_Int_Json(), Get_City_Int2_Json()])
      // console.log(cwellsData);
      // console.log(cityJsonData);
      // console.log(cityIntJsonData);
      // console.log(cityInt2JsonData);
      if (this.$route.path == "/flight") {
        //国内
        if (sessionStorage.getItem("DomesticCity")) {
          this.dealCitylist(cityJsonData.data);
        } else {
          sessionStorage.setItem("DomesticCity", JSON.stringify(cityJsonData.data));
          this.dealCitylist(cityJsonData.data);
        }
      } else {
        //国际
        if (cityInt2JsonData.data) {
          sessionStorage.setItem("InternationalCity2", JSON.stringify(cityInt2JsonData.data));
          this.dealCitylist(cityInt2JsonData.data);
          return;
        }
      }
    },
    dealCitylist (data) {
      this.citylist = data;
      //处理国内城市数据
      if (this.$route.path == "/flight") {
        //国内城市数据梳理
        this.citylist.map((v, i) => {
          v.keyName = v.CityNameChinese + "/" + v.CityNameEnglish;
        });
      } else {
        //国际
        this.citylist.map((v, i) => {
          v.CityNameChinese = v.CITY_NAME;
          v.CityNameEnglish = v.CITY_NAME_EN;
          v.LocIdCity = v.CTRY_CODE;
          v.keyName = v.CITY_NAME + "/" + v.CITY_NAME_EN;
        });
      }
    },
    onConfirm (value) {
      console.log(value);
      if (this.$route.path == "/flight") {
        if (this.cities_n == 0) {
          this.selectedCityText = value.selectedValues[0];
          this.cities[0] = value.selectedOptions[0]
        } else if (this.cities_n == 1) {
          this.cities[1] = this.selectedCityText1 = value.selectedValues[0];
          this.cities[1] = value.selectedOptions[0]
        }
      } else {
        if (this.cities_n == 0) {
          this.selectedCityText = value.selectedValues[0];
          this.cities_int[0] = value.selectedOptions[0]
        } else if (this.cities_n == 1) {
          this.cities_int[1] = this.selectedCityText1 = value.selectedValues[0];
          this.cities_int[1] = value.selectedOptions[0]

        }
      }
      this.showPicker = false;
    },
    onConfirmOne (date) {
      this.date_D[this.date_n] = new Date(date).format("yyyy-MM-dd");
      if (this.date_n == 0) this.get_back_Date(date);
      this.showPickerOne = false;
    },
    get_back_Date (date) {
      // console.log(date);
      let t = date.getTime() + 24 * 3600 * 1000;
      this.date_D[1] = new Date(t).format("yyyy-MM-dd");
    },
    onConfirmTwo (date) {
      const [start, end] = date;
      this.date_D[0] = new Date(start).format("yyyy-MM-dd");
      this.date_D[1] = new Date(end).format("yyyy-MM-dd");
      // console.log(this.date_D[0]);
      // console.log(this.date_D[1]);

      this.showPickerTwo = false;
    },
    onConfirm3 (value) {
      this.cabin = value.selectedOptions[0].text;
      this.showPicker3 = false;
    },
    // 交换城市
    changeSelectedCityText () {
      let cityText = ''
      let cityObject = {}
      cityText = this.selectedCityText
      this.selectedCityText = this.selectedCityText1
      this.selectedCityText1 = cityText
      if (this.$route.path == '/flight') {
        cityObject = this.cities[0]
        this.cities[0] = this.cities[1]
        this.cities[1] = cityObject
      } else {
        cityObject = this.cities_int[0]
        this.cities_int[0] = this.cities_int[1]
        this.cities_int[1] = cityObject
      }

    },
    formatter (day) {
      const month = day.date.getMonth() + 1;
      const date = day.date.getDate();

      const mm = new Date().getMonth() + 1;
      const dd = new Date().getDate();

      // if (month === mm) {
      //    if (date === dd) {
      //     day.text = 'today';
      //   }
      // }

      if (day.type === "start") {
        day.bottomInfo = "开始/Start";
      } else if (day.type === "end") {
        day.bottomInfo = "结束/End";
      }

      return day;
    },
    //搜索框城市过滤
    getSearchcities () {
      const cities = this.citylist;
      const filteredCities = cities.filter((city) => {
        return (
          city &&
          ((city.CityNameChinese &&
            city.CityNameChinese.includes(this.searchValue)) ||
            (city.CityNameEnglish &&
              city.CityNameEnglish.toLowerCase().includes(
                this.searchValue.toLowerCase()
              )))
        );
      });
      // console.log(filteredCities);
      // this.searchCities = filteredCities;
      return filteredCities;
    },
    handleClick () {
      this.showPicker = true;
      this.cities_n = 0;
      // this.updateSelectedCityText();
    },
    updateSelectedCityText () {
      this.selectedCityText = `${this.currentCity.CityNameChinese} (${this.currentCity.CityNameEnglish})`;
    },
    updateSelectedCityText1 () {
      this.selectedCityText1 = `${this.currentCity1.CityNameChinese} (${this.currentCity1.CityNameEnglish})`;
    },
    handleClick1 () {
      this.showPicker = true;
      this.cities_n = 1;
      // this.updateSelectedCityText1();
    },

    searchb () {
      this.$store.commit('showLoading')
      const search = {
        psg: this.$store.state.choose_psg.concat(
          this.$store.state.choose_psg_more
        ),
        cabin: this.cabin,
        type: this.cabinType(this.cabin),
        cities: this.$route.path == "/flight" ? this.cities : this.cities_int,
        date: this.date_D,
        route_active: this.router_active,
        timespan: new Date().getTime(),
        times: this.router_active,
      };
      // return
      if (!this.date_D[0]) {
        this.$notify("请选择日期");
        return;
      }
      if (this.router_active == 1) {
        let a = this.date_D[0];
        let b = this.date_D[1];
        if (!a || !b) {
          this.$notify("请选择日期");
          return;
        }
        if (
          new Date(a.replace(/-/g, "/")).getTime() >
          new Date(b.replace(/-/g, "/")).getTime()
        ) {
          this.$$notify("返程日期应在出发日期后");
          return;
        }
      }
      if (!this.cities[0] || !this.cities[1]) {
        this.$notify("请选择城市");
        return;
      }
      if (this.$store.state.choose_psg.length == 0) {
        this.$notify("请选择乘机人");
        return;
      }
      // console.log(this.$store.state.choose_psg)
      if (this.$route.path != "/internationalflight") {
        sessionStorage.setItem("search_flight", JSON.stringify(search));
      } else {
        sessionStorage.setItem("search_flight_int", JSON.stringify(search));
      }
      console.log('提交的数据', search);
      let path = this.$route.path == "/flight" ? 1 : 2;
      return
      this.$router.push({
        path: "/domesticTicket",
        query: { flight_doc_or_int: path },
      });
    },
    cabinType (cabin) {
      if (cabin == "经济舱/Economy Class") {
        return "Y";
      } else if (cabin == "公务舱/Business Class") {
        return "C";
      } else if (cabin == "头等舱/First Class") {
        return "F";
      } else {
        return 0;
      }
    },
  },
}
</script>

<style lang="scss">
.flight {
  width: 100%;
  height: 100vh;
  padding: 10px;
  box-sizing: border-box;
  background-color: #f5f5f5;
  .page_title {
    display: flex;
    text-align: center;
    box-sizing: border-box;
    .flightImg {
      margin: 0 10px;
    }
    span {
      text-align: left;
    }
    .flightTopspan {
      color: #014099;
      font-weight: 500;
      line-height: 20px;
    }
  }
  .page_cont {
    .routertype {
      background-color: white;
      border-radius: 8px;
      margin-top: 10px;
      .routetype_t {
        display: flex;
        border-bottom: solid #e4e4e4 1px;
        padding: 11px;
        div {
          width: 50%;
          padding: 0px 0px 8px 0px;
          text-align: center;
          font-size: 17px;
          color: #3f3f3f;
          span {
            font-size: 12px;
          }
        }
        .router_active {
          color: #014099 !important;
          position: relative;
          hr {
            border-bottom: #014099 solid 2px;
            width: 30px;
            position: absolute;
            right: 42%;
            bottom: -16px;
          }
        }
      }
      .search-cont {
        display: flex;
        flex-wrap: wrap;
        padding: 0 5px;
        .van-col {
          text-align: left;
          font-size: 12px;
          margin-top: 12px;
          flex-direction: column;
        }
        .van-field__control {
          font-weight: 600;
        }
        .change_city {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 100%;
        }
        .aliginright {
          width: 33%;
          margin-left: 30px;
          white-space: nowrap;
        }
        .van-col--10 {
          width: 36%;
          flex-direction: column;
          align-items: baseline;
        }
        .cont {
          border-bottom: 1px solid #e4e4e4;
          width: 100%;
          position: relative;
          overflow: hidden;
          display: flex;
          div {
            display: flex;
            justify-content: space-between;
          }
          .van-cell__value span {
            color: #323233;
            height: 16px;
            line-height: 13px;
            font-size: 13px;
            font-weight: 600;
          }
        }
        .contInt {
          .internation-box {
            padding-top: 5%;
            width: 100%;
          }

          .flag {
            position: absolute;
            /*绝对定位*/
            height: 20px;
            line-height: 20px;
            text-align: center;
            width: 62px;
            background: linear-gradient(
              -45deg,
              #f9b13b 10%,
              #f8ad38 0%,
              #f07a1b 100%
            );
            color: #fff;
          }
          .flag-left {
            -moz-transform: rotate(-45deg);
            -ms-transform: rotate(-45deg);
            -o-transform: rotate(-45deg);
            -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
            left: -19px;
            top: 5px;
          }
          .close {
            display: inline-block;
            position: absolute;
            right: 1%;
            top: 10%;
          }
        }
        .van-col .van-cell {
          padding: 0;
          margin: 5px 0;
        }
        .van-col > span {
          color: #777777;
        }
      }
    }
  }
  .next {
    height: 40px;
    width: 90%;
    background: linear-gradient(160deg, #0274c9 0%, #044db5 100%);
    border-radius: 21px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    color: white;
    font-size: 10px;
    position: absolute;
    bottom: 5%;
    left: 5%;
    text-align: center;
    .van-button--primary {
      background: linear-gradient(160deg, #0274c9 0%, #044db5 100%);
      border: none;
      font-size: 10px;
    }
  }
  // .pup {
  // }
}
</style>